<template>
    <img v-if="this.id" :src="imageSrc" class='imagePokemon' @error="setAltImg" />       
</template>

<script>
export default {
    data(){
        return {
            imageSrc : ''
        }
    },
    created(){
        this.getImage()
    },
    props:{        
        id : Number,
    },
    methods:{
        getImage: function(){
            this.imageSrc = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/official-artwork/${this.id}.png`
        },
        setAltImg() { 
            this.imageSrc = 'data:image/png;base64,'
            return false
        } 
    }
}
</script>

<style scoped>
    .imagePokemon {
        width: 100px;
        height: 100px;
        position: absolute;
        bottom: 0px;
        right: 0px;
        background-image: url('../../assets/images/pokeball.png');
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: 95% 5%; 
    }        
</style>